@font-face {
  font-family: Gilroy;
  src: url(../../../fonts/Gilroy/Gilroy-Medium.ttf);
}

* {
  font-family: Gilroy;

}

.create-news h1 {
  margin-top: 20px;
  margin-bottom: 20px;

}



button {
  margin-top: 20px;
  display: block;
  width: 100%;
  padding: 10px;
  border: none;
  border-radius: 5px;
  background-color: #1A2E47;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  box-sizing: border-box;
}

.createtags input {
  border: 2px solid #192E47;
  border-radius: 5px;
  margin-left: 5px;
  padding: 5px 20px;
  box-sizing: border-box;
  margin-bottom: 10px;
}


.createtags label{
font-size: 25px;
}

.tagslist .tags {
  margin-bottom: 10px;
  background-color: #192E47;
  border-radius: 10px;
  box-sizing: border-box;
  padding: 20px;
}
.createtags button{
  border: 2px solid #192E47;
  border-radius: 5px;
  padding: 5px 10px;
  box-sizing: border-box;
  margin-left: 10px;
  background-color: #fff;
}

.tags img{
  width: 10px;
  height: 10px;
  margin-left: 5px;
}
